.CompactCard {
    display: flex;
    flex: 1;
    height: 7rem !important;
    border-radius: 0.7rem;
    color: white;
    padding: 1rem;
    position: relative;
    cursor: pointer;
  }

  .CompactCard:hover {
    box-shadow: none!important;
  }

  
  
  .radialBar {
    flex: 1 1;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1rem;
  }
  
  
  .radialBar > span {
    font-size: 17px;
    font-weight: bold;
  }
  
  .detail {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
  }
  
  .detail > span:nth-child(2) {
    font-size: 22px;
    font-weight: bold;
  }
  .detail > span:nth-child(3) {
    font-size: 10px;
  }
  